import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database'
import { Datousuario } from '../models/datousuario'

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {
  usuariolist: AngularFireList<any> 
  selectusuario: Datousuario = new Datousuario()
  constructor(private firebare: AngularFireDatabase) { 
  }

  //Metodos
  getusuario(){
    return this.usuariolist = this.firebare.list('Usuarios')
  }

  insertusuario(usuario:Datousuario){
    this.usuariolist.push({
    name:usuario.name,
    country:usuario.country,
    date:usuario.date,
    habil:usuario.habil
    })
  }

  updateusuario(usuario:Datousuario){
    this.usuariolist.update(usuario.$key,{
      name:usuario.name,
      country:usuario.country,
      date:usuario.date,
      habil:usuario.habil
    })
  }

  delusuario($key:string){
    this.usuariolist.remove($key)
  }


}
