import { Component, OnInit } from '@angular/core';


//Servicio
import { UsuarioService } from '../../services/usuario.service'

//Clase
import { Datousuario } from '../../models/datousuario'

//Toastr
import { ToastrService } from 'ngx-toastr'

@Component({
  selector: 'app-usuarios-list',
  templateUrl: './usuarios-list.component.html',
  styleUrls: ['./usuarios-list.component.css']
})
export class UsuariosListComponent implements OnInit {
  usuarioList: Datousuario[]

  constructor(
    private usuarioService: UsuarioService,
    private toastr:ToastrService) { }

  ngOnInit(){
    this.usuarioService.getusuario()
    .snapshotChanges()
    .subscribe(item => {
      this.usuarioList = [] 
      item.forEach(element => {
         let x = element.payload.toJSON()
         x["$key"] = element.key
         this.usuarioList.push(x as Datousuario)
      });
    })
  }
  
  onEdit(usuario:Datousuario){
    this.usuarioService.selectusuario = Object.assign({},usuario)
  }
  onDelete($key:string){
    if (confirm("¿Estas seguro de eliminar el usuario?")){
      this.usuarioService.delusuario($key)
      this.toastr.success("El usuario se elimino satisfactoriamente", "Operacion Completada")
    }
  }
}
